.appBox {
	background: #fcf4fc;
	min-height: 100vh;
	height: 100%;
	margin: 0 auto;
	max-width: 1300px;
}

.appBody {
	display: flex;
}

.inputs {
	margin: 20px 8px;
	height: 84vh;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
}

@media (max-width: 900px) {
	.appBody {
		flex-direction: column;
	}
}
